/* src/components/Projects.css */

/* Make the entire page full-width, 
   but keep top padding so the navbar doesn't overlap. */
   .projects-page {
    width: 100%;
    margin: 0;
    padding-top: 100px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: #333;
  }
  
  /* Center the page heading */
  .projects-page h2 {
    text-align: center;
    margin: 0;
    padding: 2rem 0;
  }
  
  /* Each project card occupies full width 
     with padding for the text/images. */
  .project-card {
    padding: 3rem 10%;
    border: none;
    box-shadow: none;
    border-radius: 0;
    margin: 0;
  }
  
  /* Alternate backgrounds for a seamless look */
  .white-bg {
    background: #fff;
  }
  
  .grey-bg {
    background: #f7f7f7;
  }
  
  /* Project title styling */
  .project-card h3 {
    margin-bottom: 0.5rem;
    color: #007bff;
  }
  
  /* Make project text easy to read */
  .project-card p {
    line-height: 1.6;
    margin: 1rem 0;
  }
  
  /* Images container */
  .project-images {
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    margin-top: 1rem;
  }
  
  /* Thumbnail images */
  .project-images img {
    width: 200px;
    height: auto;
    cursor: pointer;
    transition: transform 0.3s;
  }
  
  /* Slight zoom on hover */
  .project-images img:hover {
    transform: scale(1.05);
  }
  
  /* Modal overlay (lightbox) */
  .modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2000;
  }
  
  /* Modal content container */
  .modal-content {
    position: relative;
    background: #fff;
    padding: 1rem;
    border-radius: 8px;
    max-width: 90%;
    max-height: 90%;
    text-align: center;
  }
  
  /* The image/arrow container inside the modal */
  .modal-image-container {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }
  
  /* Main image in the modal */
  .modal-image-container img {
    max-width: 80vw;
    max-height: 80vh;
    transition: transform 0.3s;
    cursor: pointer;
  }
  
  /* Zoomed-in effect on click */
  .zoomed {
    transform: scale(2);
  }
  
  /* Close (X) button */
  .close-button {
    position: absolute;
    top: 10px;
    right: 15px;
    background: transparent;
    border: none;
    font-size: 2rem;
    cursor: pointer;
    color: #333;
  }
  
  /* Prev/Next arrow buttons */
  .prev-button,
  .next-button {
    background: transparent;
    border: none;
    font-size: 2rem;
    cursor: pointer;
    color: #333;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    padding: 0 10px;
  }
  
  .prev-button {
    left: 0;
  }
  
  .next-button {
    right: 0;
  }
  
  /* Caption below the image in the modal */
  .modal-caption {
    margin-top: 1rem;
    font-size: 1rem;
    color: #333;
  }
  