/* src/components/Navbar.css */

.navbar {
  width: 100%;
  background: transparent;
  padding: 0.5rem 1rem;
  position: fixed;
  top: 0;
  z-index: 1000;
}

.navbar-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navbar-logo-container {
  display: flex;
  align-items: center;
}

/* Profile Pic */
.navbar-profile-pic {
  height: 40px;
  width: 40px;
  object-fit: cover;
  border-radius: 50%;
  margin-right: 0.75rem;
  border: 2px solid #007bff;
}

.navbar-logo {
  color: #fff;
  font-size: 1.8rem;
  font-weight: bold;
  font-family: 'Baloo 2', cursive, sans-serif;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.7);
}

.navbar-menu {
  list-style: none;
  display: flex;
  gap: 3rem;
  margin-right: 6rem;
}

/* Menu Links */
.navbar-menu li a {
  color: #007bff;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s;
  font-family: 'Baloo 2', cursive, sans-serif;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.7);
}

.navbar-menu li a:hover {
  color: #0056b3;
}
